<template>
    <div class="section">
        <d3-timeline :data="data"></d3-timeline>

        <p class="has-text-centered title">data</p>
        <pre>
            <code>
                {{JSON.stringify(data, null, 4)}}
            </code>
        </pre>

        <p class="has-text-centered title">options</p>
        <pre>
            <code>
                {{JSON.stringify(options, null, 4)}}
            </code>
        </pre>

        <p class="has-text-centered title">className List</p>
        <p class="has-text-centered subtitle">You can also define your own class like this</p>
        <pre>
            <code>
                .d3-time-line .entry--interval--default {
                    fill: #6eadc1;
                    stroke: #6eadc1;
                    fill-opacity: 0.125;
                    stroke-opacity: 1;
                    cursor: pointer;
                }

                .d3-time-line .entry--point--default {
                    fill: #6eadc1;
                    stroke: #6eadc1;
                    fill-opacity: 0.125;
                    stroke-opacity: 1;
                    cursor: pointer;
                }

                .d3-time-line .entry--interval--default text {
                    fill: #000;
                }

                .d3-time-line .entry--interval--error {
                    fill: #ff3860;;
                    stroke: #ff3860;
                    fill-opacity: 0.125;
                    stroke-opacity: 1;
                    cursor: pointer;
                }

                .d3-time-line .entry--point--error {
                    fill: #ff3860;;
                    stroke: #ff3860;
                    fill-opacity: 0.125;
                    stroke-opacity: 1;
                    cursor: pointer;
                }

                .d3-time-line .entry--interval--error text {
                    fill: #fff;
                }

                .d3-time-line .entry--interval--success {
                    fill: #23d160;;
                    stroke: #23d160;
                    fill-opacity: 0.125;
                    stroke-opacity: 1;
                    cursor: pointer;
                }


                .d3-time-line .entry--point--success {
                    fill: #23d160;;
                    stroke: #23d160;
                    fill-opacity: 0.125;
                    stroke-opacity: 1;
                    cursor: pointer;
                }

                .d3-time-line .entry--interval--success text {
                    fill: #fff;
                }

                .d3-time-line .entry--interval--info {
                    fill: #167df0;;
                    stroke: #167df0;
                    fill-opacity: 0.125;
                    stroke-opacity: 1;
                    cursor: pointer;
                }

                .d3-time-line .entry--point--info {
                    fill: #167df0;;
                    stroke: #167df0;
                    fill-opacity: 0.125;
                    stroke-opacity: 1;
                    cursor: pointer;
                }

                .d3-time-line .entry--interval--info text {
                    fill: #fff;
                }

                .d3-time-line .entry--interval--warn {
                    fill: #ffdd57;;
                    stroke: #ffdd57;
                    fill-opacity: 0.125;
                    stroke-opacity: 1;
                    cursor: pointer;
                }

                .d3-time-line .entry--point--warn {
                    fill: #ffdd57;;
                    stroke: #ffdd57;
                    fill-opacity: 0.125;
                    stroke-opacity: 1;
                    cursor: pointer;
                }

                .d3-time-line .entry--interval--warn text {
                    fill: rgba(0,0,0,.7);
                }
            </code>
        </pre>
    </div>
</template>

<script>
    export default {
        name: "d3TimelineDemo",
        data() {
            return {
                data: [{
                    group: 'Name',
                    at: new Date([2016, 5, 1]).valueOf(),
                    symbol: 'symbolCircle',
                    title: 'kjlqwjq',
                    className: 'entry--point--warn'
                }, {
                    group: 'Name',
                    at: new Date([2016, 5, 1]).valueOf(),
                    title: 'wlqjlw',
                    className: 'entry--point--success',
                    symbol: 'symbolWye'
                }, {
                    group: 'Name',
                    at: new Date([2016, 7, 1]).valueOf(),
                    className: 'entry--point--error',
                    title: 'lqwpqow',
                    symbol: 'symbolSquare'
                }, {
                    group: 'Name',
                    at: new Date([2016, 8, 1]).valueOf(),
                    className: 'entry--point--error',
                    symbol: 'symbolDiamond',
                    title: 'wopqowp'
                }, {
                    group: 'Name',
                    at: new Date([2016, 9, 1]).valueOf(),
                    title: 'qoiwiu',
                    className: 'entry--point--error',
                    symbol: 'symbolTriangle'
                }, {
                    group: 'Name',
                    at: new Date([2016, 10, 1]).valueOf(),
                    symbol: 'symbolSquare',
                    title: 'poiwsklajs'
                }, {
                    group: 'Type',
                    at: new Date([2016, 5, 11]).valueOf(),
                    title: 'owqpwpqw',
                    className: 'entry--point--success',
                    symbol: 'symbolSquare',
                }, {
                    group: 'Type',
                    at: new Date([2016, 5, 15]).valueOf(),
                    className: 'entry--point--success',
                    symbol: 'symbolCross',
                    title: 'qpwiqiwpo'
                }, {
                    group: 'Type',
                    at: new Date([2016, 7, 10]).valueOf(),
                    className: 'entry--point--success',
                    symbol: 'symbolCross',
                    title: 'wqpwipqw'
                }, {
                    group: 'Type',
                    label: "I\'m a label with a custom class",
                    from: new Date([2016, 6, 1]).valueOf(),
                    to: new Date([2016, 7, 1]).valueOf(),
                    title: "This is my title"
                }, {
                    group: 'Type',
                    at: new Date([2016, 10, 1]).valueOf(),
                    title: 'wqwqwipqw',
                    className: 'entry--point--warn',
                    symbol: 'symbolStar'
                }, {
                    group: 'Type',
                    symbol: 'symbolStar',
                    at: new Date([2016, 11, 1]).valueOf(),
                    title: 'wqsjksals'
                }, {
                    group: 'Imp',
                    label: "I'am a label of class 'entry--interval--error'",
                    from: new Date([2016, 5, 15]).valueOf(),
                    to: new Date([2016, 7, 1]).valueOf(),
                    className: 'entry--interval--error'
                }, {
                    group: 'Imp',
                    label: "I'am a label of class 'entry--interval--success'",
                    from: new Date([2016, 8, 1]).valueOf(),
                    to: new Date([2016, 9, 12]).valueOf(),
                    className: 'entry--interval--success'
                }],
                options: {
                    // interval config
                    intervalCornerRadius: 4,

                    // symbol config
                    symbolSize: 200,

                    // group config
                    groupLabelFontSize: 14,
                    groupLabelFontWeight: 400,

                    // group label config
                    groupLaneWidth: 80,

                    // axisX config
                    axisXHeight: 30,
                    axisXFontSize: 12,
                    axisXFontWeight: 400,

                    // axisX Label config
                    axisXLabel: 'Key',
                    axisXLabelHeight: 30,
                    axisXLabelFontSize: 14,
                    axisXLabelFontWeight: 600,

                    // background config
                    backgroundColor: 'rgba(192, 192, 192, 0.125)',
                    borderRadius: 10,
                    borderWidth: 2,
                    borderColor: '#ccc',

                    // lines config
                    boundingLineWidth: 2,
                    boundingLineColor: '#ccc',

                    // current time line reference
                    currentTimeLineWidth: 2,
                    currentTimeLineColor: 'red'
                }
            }
        }
    }
</script>

<style>
    .d3-time-line .entry--interval--default {
        fill: #6eadc1;
        stroke: #6eadc1;
        fill-opacity: 0.125;
        stroke-opacity: 1;
        cursor: pointer;
    }

    .d3-time-line .entry--point--default {
        fill: #6eadc1;
        stroke: #6eadc1;
        fill-opacity: 0.125;
        stroke-opacity: 1;
        cursor: pointer;
    }

    .d3-time-line .entry--interval--error {
        fill: #ff3860;;
        stroke: #ff3860;
        fill-opacity: 0.125;
        stroke-opacity: 1;
        cursor: pointer;
    }

    .d3-time-line .entry--point--error {
        fill: #ff3860;;
        stroke: #ff3860;
        fill-opacity: 0.125;
        stroke-opacity: 1;
        cursor: pointer;
    }

    .d3-time-line .entry--interval--success {
        fill: #23d160;;
        stroke: #23d160;
        fill-opacity: 0.125;
        stroke-opacity: 1;
        cursor: pointer;
    }

    .d3-time-line .entry--point--success {
        fill: #23d160;;
        stroke: #23d160;
        fill-opacity: 0.125;
        stroke-opacity: 1;
        cursor: pointer;
    }

    .d3-time-line .entry--interval--info {
        fill: #167df0;;
        stroke: #167df0;
        fill-opacity: 0.125;
        stroke-opacity: 1;
        cursor: pointer;
    }

    .d3-time-line .entry--point--info {
        fill: #167df0;;
        stroke: #167df0;
        fill-opacity: 0.125;
        stroke-opacity: 1;
        cursor: pointer;
    }

    .d3-time-line .entry--interval--warn {
        fill: #ffdd57;;
        stroke: #ffdd57;
        fill-opacity: 0.125;
        stroke-opacity: 1;
        cursor: pointer;
    }

    .d3-time-line .entry--point--warn {
        fill: #ffdd57;;
        stroke: #ffdd57;
        fill-opacity: 0.125;
        stroke-opacity: 1;
        cursor: pointer;
    }
</style>



