import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import d3Pie from '@/components/d3Pie'
import d3Line from '@/components/d3Line'
import d3Bar from '@/components/d3Bar'
import d3ProgressArc from '@/components/d3ProgressArc'
import d3SankeyCircular from '@/components/d3SankeyCircular'
import d3Timeline from '@/components/d3Timeline'
import d3Timelion from '@/components/d3Timelion'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'HelloWorld',
            component: HelloWorld
        },
        {
            path: '/d3-pie',
            name: 'd3Pie',
            component: d3Pie
        },
        {
            path: '/d3-bar',
            name: 'd3Bar',
            component: d3Bar
        },
        {
            path: '/d3-line',
            name: 'd3Line',
            component: d3Line
        },
        {
            path: '/d3-timeline',
            name: 'd3Timeline',
            component: d3Timeline
        },
        {
            path: '/d3-timelion',
            name: 'd3Timelion',
            component: d3Timelion
        },
        {
            path: '/d3-progress-arc',
            name: 'd3ProgressArc',
            component: d3ProgressArc
        },
        {
            path: '/d3-sankey-circular',
            name: 'd3SankeyCircular',
            component: d3SankeyCircular
        }
    ]
})



