<template>
    <div class="section">
        <d3-progress-arc :data="data"></d3-progress-arc>

        <p class="has-text-centered title">data</p>
        <pre>
            <code>
                {{JSON.stringify(data, null, 4)}}
            </code>
        </pre>

        <p class="has-text-centered title">options</p>
        <pre>
            <code>
                {{JSON.stringify(options, null, 4)}}
            </code>
        </pre>
    </div>
</template>

<script>
    export default {
        name: "d3ProgressArcDemo",
        data() {
            return {
                data: [{
                    angle: Math.PI * 2,
                    text: 'key1',
                }, {
                    angle: Math.PI * 2 * 23 / 24,
                    text: 'key2',
                }, {
                    angle: Math.PI * 2 * 23 / 24 * 23 / 24,
                    text: 'key3'
                }, {
                    angle: Math.PI * 2 * 23 / 24 * 23 / 24 * 23 / 24,
                    text: 'key4'
                }, {
                    angle: Math.PI * 2 * 23 / 24 * 23 / 24 * 23 / 24 * 23 / 24,
                    text: 'key5'
                }, {
                    angle: Math.PI * 2 * 23 / 24 * 23 / 24 * 23 / 24 * 23 / 24 * 23 / 24,
                    text: 'key6'
                }].reverse(),
                options: {
                    innerRadius: 40,
                    cornerRadius: 8,
                    circleRadius: 4,
                    textMarginLeft: 2,
                    textFontSize: 16,
                    animationDuration: 2000,
                    animationDelay: 50
                }
            }
        }
    }
</script>

<style scoped>

</style>



