<template>
    <div class="section">
        <d3-bar :data="data"></d3-bar>

        <p class="has-text-centered title">data</p>
        <pre>
            <code>
                {{JSON.stringify(data, null, 4)}}
            </code>
        </pre>

        <p class="has-text-centered title">options</p>
        <pre>
            <code>
                {{JSON.stringify(options, null, 4)}}
            </code>
        </pre>
    </div>
</template>

<script>
    export default {
        name: "d3BarDemo",
        data() {
            return {
                data: [
                    {key: 'jiang', value: 80},
                    {key: 'zeng', value: 30},
                    {key: 'ammar', value: 60},
                    {key: 'tu', value: 40},
                ],
                options: {
                    // bar config
                    fill: '#6eadc1',
                    stroke: '#6eadc1',
                    fillOpacity: 0.6,
                    strokeOpacity: 1,
                    barTitle: d => d.value,

                    // axis font config
                    axisFontSize: 12,
                    axisFontWeight: 400,

                    // axis label config
                    axisYLabel: 'Value',
                    axisXLabel: 'Key',
                    axisXLabelHeight: 20,
                    axisYLabelWidth: 20,

                    // axis label font config
                    axisLabelFontSize: 12,
                    axisLabelFontWeight: 400,
                    axisLabelFontOpacity: 0.5,

                    // axis lane config
                    axisXHeight: 25,
                    axisYWidth: 35,

                    isVertical: false,
                }
            }
        }
    }
</script>

<style scoped>

</style>



