<template>
    <div class="section">
        <d3-line :data="data"></d3-line>

        <p class="has-text-centered title">data</p>
        <pre>
            <code>
                {{JSON.stringify(data, null, 4)}}
            </code>
        </pre>

        <p class="has-text-centered title">options</p>
        <pre>
            <code>
                {{JSON.stringify(options, null, 4)}}
            </code>
        </pre>
    </div>
</template>

<script>
    export default {
        name: "d3LineDemo",
        data() {
            return {
                data: [
                    {key: 'jiang', value: 80},
                    {key: 'zeng', value: 30},
                    {key: 'ammar', value: 60},
                    {key: 'tu', value: 40},
                ],
                options: {
                    // line config
                    stroke: 'rgb(188, 82, 188)',
                    strokeWidth: 2,

                    // axis config
                    axisXHeight: 25,
                    axisYWidth: 35,
                    axisFontSize: 14,

                    // circle config
                    circleRadius: 5,
                    circleColor: 'rgb(188, 82, 188)',

                    // tooltip config
                    circleTitle: d => d.value,

                    // curve config
                    curve: 'curveCardinal',

                    // axis label config
                    axisXLabel: 'Key',
                    axisYLabel: 'Value',
                    axisXLabelHeight: 30,
                    axisYLabelWidth: 20,
                    axisLabelFontSize: 12,
                    axisLabelFontWeight: 400,
                    axisLabelFontOpacity: 0.5
                }
            }
        }
    }
</script>

<style scoped>

</style>



