<template>
    <div class="section">
        <d3-pie :data="data" :options="options"></d3-pie>

        <p class="has-text-centered">template</p>
        <pre>
            <code>
                {{tpl}}
            </code>
        </pre>

        <p class="has-text-centered">data</p>
        <pre>
            <code>
                {{JSON.stringify(data, null, 4)}}
            </code>
        </pre>

        <p class="has-text-centered">options</p>
        <pre>
            <code>
                {{JSON.stringify(options, null, 4)}}
            </code>
        </pre>
    </div>
</template>

<script>
    export default {
        name: "d3PieDemo",
        data() {
            return {
                tpl: "<d3-pie :data='data' :options='options'></d3-pie>",
                data: [
                    {key: 'jiang', value: 80},
                    {key: 'zeng', value: 30},
                    {key: 'ammar', value: 60},
                    {key: 'tu', value: 40},
                ],
                options: {
                    // pie size config
                    innerRadius: 40,
                    cornerRadius: 0,

                    // gap between arcs config
                    padAngle: 0.01,

                    // tooltip config
                    arcTitle: d => d.data.value,

                    // arc label
                    arcLabel: d => d.data.key,

                    // axisX label config
                    axisXLabelHeight: 30,
                    axisXLabel: 'Key',
                    axisXLabelFontSize: 12,
                    axisXLabelFontWeight: 400,
                    axisXLabelFontOpacity: 0.5,

                    // arc label config
                    arcLabelFontSize: 10,
                    arcLabelFontOpacity: 0.5,

                    // animation config
                    animationDuration: 1000
                }
            }
        }
    }
</script>

<style scoped>

</style>



